html, body {
  height: 100vh;
  margin: 0;
}

#root {
  min-height: 100vh; 
}

.App {
  height: 100vh;
  width: inherit;
}

.main {
  display: flex;
  flex-direction: row;
  height: 100vh;
  width: inherit;
}
.side-bar {
  width: 40vh;
  height: 100%;
  background-color: #282c34;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: white;
}

.circle-crop {
  margin-top: 5vh;
  width: 192px; 
  height: 192px;
  position: relative;
  overflow: hidden;
  border-radius: 50%;
}

img {
  display: inline;
  margin: 0 auto;
}

.display {
  width: 100%;
  height: 100%;
  background-color: #dde4eb;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.javascript-fun-stuffs {
  font-family: Roboto;
}